body {
  background: rgb(128,0,32);
  background: -moz-linear-gradient(90deg, rgba(128,0,32,1) 35%, rgba(0,0,0,1) 100%);
  background: -webkit-linear-gradient(90deg, rgba(128,0,32,1) 35%, rgba(0,0,0,1) 100%);
  background: linear-gradient(90deg, rgba(128,0,32,1) 35%, rgba(0,0,0,1) 100%);  
}

#nvLogo {
  position: relative;
  top: 0px;
  max-width: 150px;
}

.navLinks {
  text-decoration: none;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  display: inline-flex;
  justify-content: space-between;
  margin: 15px 10px;
  padding: 3px 3px;
  float: right;
  color: #E1C699;
  font-size: 18px;
  transition: color 0.3s;
  background: black;
  border-radius: 3px;
}

.navLinks:hover {
  color: white;
}
.navLinks:after {
  transition: 0.2s
}





