.card {
  margin: 0 5px;
}
.card-body {
  background-color: black;
}

.card-footer {
  background-color: black;
}

#projectsTitleText {
  text-align: center;
  text-shadow: 3px 3px 3px black;
  font-family: 'Playfair Display', serif;
  font-size: 60px;
  font-weight: bolder;
  font-style: italic;
}

.projectsText {
  text-align: center;
  color: #E1C699;
}

.siteLink {
  margin: 0 75px;
}

a {
  text-decoration: none;
  color: #D8BCAB;
  transition: color 0.5s ease;
}

a:hover {
  color: white;
}

